.check{
  padding-left: 1.2em;
  margin-top: 10px;
}

.check__inp{
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.check__box{
  position: absolute;
  width: 1em;
  height: 1em;
  background-color: rgba(29, 58, 160, 0.9);
  border: 1px solid var(--white);
  margin-left: -1.2em;
  margin-top: 3px;
  border-radius: 4px;
}


// Все состояния //

// Фокус //

.check__inp:checked + .check__box{
  background-color: var(--lemon);
  background-image: url(../img/svg/check.svg);
  border: 1px solid var(--lemon);
  background-repeat: no-repeat;
  background-position: center;
}

.check__inp:focus + .check__box{
  border: 1px solid var(--lemon);
}

// Запрещено //

.check__inp:disabled + .check__box{
  border: 1px solid var(--mystic-72);
}

.check__inp:checked:disabled + .check__box{
  border: 1px solid var(--mystic-72);
}
